import React, { Component } from 'react';
import { connect } from 'react-redux'
import { withStyles, createStyleSheet } from 'material-ui/styles';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import Button from 'material-ui/Button';
import TextField from 'material-ui/TextField';
import { pink, grey } from 'material-ui/colors';
import Img from '../../../../../images/profile.jpg';
import ImageAvatars from '../../../components/ImageAvatars';
import { updateProfile } from '../modules/profile';

const styles = {
  textWidth:{
    width: '40%',
    minWidth: '200px',
  },
  button: {
    margin: '20px',
  },
  bigAvatar: {
    width: 60,
    height: 60,
  },
  textShow: {
    textAlign: 'center',
  },
  table: {
    margin: '0 auto',
    textAlign: 'left',
  },
  label: {
    fontSize:'0.8em',
    color:'#888',
    paddingTop: '20px',
  },
  userValue: {
    borderBottom:'1px solid #999',
    paddingBottom:'5px',
    paddingTop: '5px',
  },
};

class Profile extends Component {
  constructor(props) {
    super(props);
    this.state = {
      onEditing: false,
      name: '',
      location: '',
      company: '',
      id: '',
      email: '',
    };
  }

  componentDidMount() {
    this.props.userProfile.userProfile && this.setState({
      name: this.props.userProfile.userProfile.userInfo.name,
      email: this.props.userProfile.userProfile.userInfo.email,
      id: this.props.userProfile.userProfile.userInfo.id,
      company: this.props.userProfile.userProfile.userInfo.company,
      location: this.props.userProfile.userProfile.userInfo.location,
    });
  }

  // 
  handleChange = name => event => {
    this.setState({
      [name]: event.target.value,
    });
    if (name === 'confirmPassword') {
      if (event.target.value === this.state.password) {
        this.setState({
          error: false,
        });
      }
    }
  }

  changeEditStatus = () => {
    this.setState({
      onEditing: !this.state.onEditing,
    });
  }

  onSubmit = () => {
    this.props && this.props.updateProfile(this.state.id, this.state.name, this.state.location, this.state.company);
  }

  _renderProfileEdit = (userProfile) => {
    return(
      <div>
        <div>
          <TextField className={this.props.classes.textWidth}
            defaultValue={userProfile.name}
            id="Name"
            label="Name"
            margin="normal"
            autoFocus
            onChange={this.handleChange('name')}
          />
        </div>
        <div>
          <TextField className={this.props.classes.textWidth}
            defaultValue={userProfile.email}
            id="Email"
            label="Email"
            margin="normal"
            disabled
            onChange={this.handleChange('email')}
          />
        </div>
        <div>
          <TextField className={this.props.classes.textWidth}
            defaultValue={userProfile.location}
            id="Location"
            label="Location"
            margin="normal"
            onChange={this.handleChange('location')}
          />
        </div>
        <div>
          <TextField className={this.props.classes.textWidth}
            defaultValue={userProfile.company}
            id="Company"
            label="Company"
            margin="normal"
            onChange={this.handleChange('company')}
          />
        </div>
        <Button
          raised
          color="primary"
          className={this.props.classes.button}
          onClick={this.onSubmit}
        >
          update
        </Button>
        <Button
          raised
          color="accent"
          className={this.props.classes.button}
          onClick = {this.changeEditStatus}
        >
          cancel
        </Button>
      </div>
    )
  }

  _renderProfileView = (userProfile) => {
    return (
      <div>
        <table className={classNames(this.props.classes.table, this.props.classes.textWidth)}>
          <tbody>
            <tr>
              <td className={this.props.classes.label}>
                Name
              </td>
            </tr>
            <tr>
              <td className={this.props.classes.userValue}>
                {userProfile.name}
              </td>
            </tr>
            <tr>
              <td className={this.props.classes.label}>
                Email
              </td>
            </tr>
            <tr>
              <td className={this.props.classes.userValue}>
                {userProfile.email}
              </td>
            </tr>
            <tr>
              <td className={this.props.classes.label}>
                Location
              </td>
            </tr>
            <tr>
              <td className={this.props.classes.userValue}>
                {userProfile.location}
              </td>
            </tr>
            <tr>
              <td className={this.props.classes.label}>
                Company
              </td>
            </tr>
            <tr>
            <td className={this.props.classes.userValue}>
                {userProfile.company}
              </td>
            </tr>
          </tbody>
        </table>
        <Button
          raised
          color="primary"
          className={this.props.classes.button}
          onClick = {this.changeEditStatus}
        >
          edit
        </Button>
        <Button
          raised
          color="accent"
          className={this.props.classes.button}
        >
          logout
        </Button>
      </div>
    )
  }

  render() {
    const userInfo = {
      id: this.state.id,
      name: this.state.name,
      company: this.state.company,
      location: this.state.location,
      email: this.state.email,
    }
    return (
      <div>
        <h3>Your Information:</h3>
        <div className={this.props.classes.textShow}>
          <ImageAvatars UserImg={Img} />
          { this.state.onEditing ? (
            <div>
              {
                this._renderProfileEdit(userInfo)
              }
            </div>
          ) :
            (
              <div>
                {
                  this._renderProfileView(userInfo)
                }  
              </div>
            )}
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = {
  updateProfile,
}

const mapStateToProps = (state) => ({
  userProfile : state.login,
  // update : state.profile,
})

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(createStyleSheet(styles))(Profile));



