import axios from 'axios';
import { devServerAddress } from '../../../global/const';

// ------------------------------------
// Constants
// ------------------------------------
export const UPDATE_PROFILE_START = 'UPDATE_PROFILE_START';
export const UPDATE_PROFILE_DONE = 'UPDATE_PROFILE_DONE';

// ------------------------------------
// Actions
// ------------------------------------
export function updateProfileStart() {
  return {
    type: UPDATE_PROFILE_START,
    message: 'Start updating user profile data...',
  };
}

export function updateProfileDone(users, msg) {
  return {
    type: UPDATE_PROFILE_DONE,
    userProfile: users,
    message: msg,
  };
}

export function updateProfile(id, name, location, company) {
  return (dispatch, getState) => {
    dispatch(updateProfileStart());
    axios.post(
      `${devServerAddress}/account/profile`,
      {
        id,
        name,
        location,
        company,
      })
      .then((response) => {
        console.log('', response);
        if (response.data.status === 'success') {
          return dispatch(updateProfileDone(null, response.data.msg));
        } else if (response.data.status === 'failure') {
          return dispatch(updateProfileDone(null, response.data.msg));          
        }
      })
      .catch((error) => {
        return dispatch(updateProfileDone(null, 'Network error.'));
      });
  };
}

// ------------------------------------
// Action Handlers
// ------------------------------------

const USERS_ACTION_HANDLERS = {
  [UPDATE_PROFILE_START]: (state, action) => {
    return ({ ...state, message: action.message });
  },
  [UPDATE_PROFILE_DONE]: (state, action) => {
    return ({ ...state, userProfile: action.userProfile, message: action.message });
  },
};

// ------------------------------------
// Reducer
// ------------------------------------
const initialState = {
  userProfile: null,
  message: '',
};

export default function updateUserReducer(state = initialState, action) {
  const handler = USERS_ACTION_HANDLERS[action.type];
  return handler ? handler(state, action) : state;
}



